 <template>
    <div class="page-container" v-show="isShowContainer">
        <div class="page-main" v-for="item in dataArtical" v-on:click="goToDetail">
            <h1 class="page-titile">{{item.pageTitle}}</h1>
            <div class="page-user-info">
                <p class="page-p">
                    <img :src="avatar" class="page-user-img">
                    <span class="page-username">{{item.userName}}</span>
                </p>
                <p class="page-p">
                    <span class="page-user-time">{{item.pageTime}}</span>
                </p>
            </div>
            <div class="page-artical-msg">
                <P>{{item.pageInfo}}</P>
            </div>
        </div>
<!--         <res-info v-bind:post="posts" v-show="isChildShow" v-on:child-event="showMessageFromChild"></res-info> -->
    </div>
</template>
<script>
    import {mapMutations} from 'vuex'
    export default {
        data() {
            return {
                isShowContainer: false,
                isActive: false,
                avatar: require('../../common/images/avater.jpg'),
                dataArtical: [
                    {"pageTitle": "静态类型检查工具Flow", "img": require('../../common/images/avater.jpg'), "userName": "寻找许仙", "pageInfo": "&nbsp&nbsp在Vue源码里，尤大神采用了Flow作为静态类型检查，Flow是facebook出品的静态类型检查工具。为什么要用Flow？", "pageTime": "2018-09-30"},
                    {"pageTitle": "静态类型检查工具Flow", "img": require('../../common/images/avater.jpg'), "userName": "寻找许仙", "pageInfo": "&nbsp&nbsp在Vue源码里，尤大神采用了Flow作为静态类型检查，Flow是facebook出品的静态类型检查工具。为什么要用Flow？", "pageTime": "2018-09-30"},
                    {"pageTitle": "静态类型检查工具Flow", "img": require('../../common/images/avater.jpg'), "userName": "寻找许仙", "pageInfo": "&nbsp&nbsp在Vue源码里，尤大神采用了Flow作为静态类型检查，Flow是facebook出品的静态类型检查工具。为什么要用Flow？", "pageTime": "2018-09-30"},
                    {"pageTitle": "静态类型检查工具Flow", "img": require('../../common/images/avater.jpg'), "userName": "寻找许仙", "pageInfo": "&nbsp&nbsp在Vue源码里，尤大神采用了Flow作为静态类型检查，Flow是facebook出品的静态类型检查工具。为什么要用Flow？", "pageTime": "2018-09-30"}
                ]
            }
        },
        methods: {
            mainTransition(e) {
                this.isActive = !this.isActive;
                this.isUl = !this.isUl;
            },
            // ...mapMutations({
            //     setTitle: 'SET_SINGERS' // SET_SINGERS 区别常量名 修改标题
            // }),
            goToDetail() {
                this.$router.push({name: 'mainPageDetail'});
            }
        },
        activated() { // keep-alive 组件激活时调用。
            this.$store.commit('SET_TITLES', '首页'); // 该方法比较靠谱
            this.$store.commit('SET_RESINFOS', '12323'); // 该方法比较靠谱
            this.$store.commit('SET_ISCHILDSHOWS', true); // 该方法比较靠谱
        },
        mounted() {
            var self = this;
            self.isShowContainer = true;
           // this.setTitle('文章一');
        },
        components: {
            // 'res-info': resInfo
        },
    }
</script>
<style scoped>
@import '../../common/css/mainPage.styl'; /*引入样式*/
</style>


